  obtainDataChartLine() {

    let idMachine = $('#machines').val() != undefined ? $('#machines').val() : 1;
    let typeChart = $('#typeChart').val();
    let groupBy = $('#groupByChart').val();

    let startDate = $('#startDate .mx-datepicker .mx-input').val();
    // startDate += " " + getActualHour();
    startDate += " " + $('#hourChart').val();

    let endedDate = $('#endedDate .mx-datepicker .mx-input').val();
    // endedDate += " " + getActualHour();
    endedDate += " " +  $('#hourChart').val();

    let urlGroupBy;
    switch (groupBy) {

      case "diesel":
        urlGroupBy = urlApi + '/obtainAcumuladoDiesel';
        break;

      case "kilometer":
        urlGroupBy = urlApi + '/obtainAcumuladoKilometers';
        break;
    }

    let dataRes;
    let arranque;
    let operacion;
    let nivelTanque = 0;
    let newMinutes = [];

    $.ajax({
      url: urlGroupBy,
      type: 'GET',
      async: false,
      data: {
        idMachine,
        startDate: startDate,
        endedDate: endedDate,
        type: typeChart
      },
      success: function (response) {
        // console.log( "Line Chart", response );

        let dateFlag = "";
        dataRes = response[0].filter(data => {

          if(data.Date_flag != dateFlag){
            dateFlag = data.Date_flag;
            return data.STATUS_TANQUE;
          }
        });

        let maximumValue;
        let mediumValue;
        switch(typeChart){

          case "hour":
            dataRes = minutes().map(minute => {
              let temp = dataRes.filter(data => {
                if(data.GROUPER === minute){
                  newMinutes.push(minute);
                  return data;
                }
              });

              return { STATUS_TANQUE: temp.length > 0 ? temp[0].STATUS_TANQUE : 0, arranque: temp.length > 0 ? temp[0].arranque : 0, operacion: temp.length > 0 ? temp[0].operacion : 0 };
            });

            dataRes = dataRes.filter( data => { if(data.STATUS_TANQUE > 0) return data } );

            // maximumValue = ( Math.ceil( (dataRes[0].STATUS_TANQUE / 85) ) * 100 );
            // mediumValue = ( Math.ceil( (dataRes[0].STATUS_TANQUE / 185) ) * 100 );

            maximumValue = ( Math.ceil( (response[1] / 85) ) * 100 );
            mediumValue = ( Math.ceil( (response[1] / 185) ) * 100 );

            arranque = dataRes.map( data => { if(data.arranque == 1) {return maximumValue} else {return  0} } );
            operacion = dataRes.map( data => { if(data.operacion == 1) {return mediumValue} else {return  0} } );

            dataRes = dataRes.map( data => data.STATUS_TANQUE );

            break;

          case "day":

            dataRes = hoursArray.map(hour => {
              let temp = dataRes.filter(data => {
                if(data.GROUPER === hour){
                  return data;
                }
              });
              return { STATUS_TANQUE: temp.length > 0 ? temp[0].STATUS_TANQUE : 0, arranque: temp.length > 0 ? temp[0].arranque : 0, operacion: temp.length > 0 ? temp[0].operacion : 0 };
            });

            maximumValue = ( Math.ceil( (response[1] / 95) ) * 100 );
            mediumValue = ( Math.ceil( (response[1] / 195) ) * 100 );

            arranque = dataRes.map( data => { if(data.arranque == 1) {return maximumValue} else {return  0} } );
            operacion = dataRes.map( data => { if(data.operacion == 1) {return mediumValue} else {return  0} } );

            dataRes = dataRes.map(data => data.STATUS_TANQUE);
            break;

          case "month":

            dataRes = days.map((hour) => {
              let temp = dataRes.filter(data => {
                if(data.GROUPER === hour)
                  return data;
              });
              return temp.length > 0 ? temp[0].STATUS_TANQUE : 0;
            });
            break;

          case "year":
            dataRes = year.map((month) => {
              let temp = dataRes.filter(data => {
                if(data.GROUPER === month)
                  return data;
              });
              return temp.length > 0 ? temp[0].STATUS_TANQUE : 0;
            });
            break;
        }

        // dataRes = dataRes.map(data => data.STATUS_TANQUE);
        nivelTanque = response[1];
      },
      error: function (ex) {
        console.error(ex);
        showAlarms('4', 'nc-icon nc-notification-70', ex);
        registrarNotificacion(idMachine, ex, "Error");
      }
    });

    return [dataRes, nivelTanque, newMinutes, arranque, operacion];
  },

