  async obtainCoordinatesTrains(intevalAdvance) {

    intevalAdvanceGbl = intevalAdvance;

    // let startDate = $('#startDate .mx-datepicker .mx-input').val();
    // let endedDate = $('#endedDate .mx-datepicker .mx-input').val();

    let startDate = _today;
    let endedDate = _today;

    let idMachine = $('#machines').val() != undefined ? $('#machines').val() : 1;

    dataCoordinatesProd = [];

    await $.ajax({
      url: urlApi + "/obtainDataGps",
      type: 'GET',
      async: true,
      data: {
        id: lastId, startDate, endedDate, idMachine, firstRead
      },
      success: function (response) {
        // console.log(response);

        let td = response.length;

        if(td == 0)
          return;

        try {

          markers = obtainDataMarkets(response[1].latitud, response[0].latitud, response[1].longitud, response[0].longitud, response[1].timestamp_PLC, response[0].timestamp_PLC, response[0].tnq_porc, response[0].nivel_tanque, response[0].arranque, response[0].operacion, response[0].fechaPLC);

          lastId = response[0].id;

          dataCoordinates = response.map(data => { return {lat: data.latitud, lng: data.longitud} });

          let arrayProd = [];
          response.map( data => {

            if(data.operacion == 1){
              arrayProd.push( { lat: data.latitud, lng: data.longitud } );

            }else{

              dataCoordinatesProd.push( arrayProd );
              arrayProd = [];
            }
          });

          dataCoordinatesProd = dataCoordinatesProd.filter(data => { if(data.length > 0 ) return data; } );

        } catch (error) {
          console.error(error);
        }
      },
      error: function (ex) {

        console.error(ex);
        showAlarms('4', 'nc-icon nc-notification-70', ex.statusCode + " - " + ex.statusText);
        registrarNotificacion(idMachine, ex, "Error");
      }
    });

    return JSON.stringify([dataCoordinates, markers, dataCoordinatesProd]);
  },

