/**
 * This function set the data to a array
 */

function setDataToArray(response, groupBy) {

  let arrayData = [];
  let saveVal = true;

  switch (groupBy) {

    case "day":
      saveVal = true;
      for (let i = 0; i < 24; i++) {

        $.each(response, function (index, value) {

          if (value.label == i.toString()) {
            arrayData.push(value.acumulado);
            saveVal = false;
            return false;
          } else {
            saveVal = true;
          }
        });

        if (saveVal)
          arrayData.push(0);
      }
      break;

    case "month":
      saveVal = true;
      let daysMonth = daysInMonth().length;

      for (let i = 0; i < daysMonth; i++) {

        $.each(response, function (index, value) {

          if (value.label == i.toString()) {
            arrayData.push(value.acumulado);
            saveVal = false;
            return false;
          } else {
            saveVal = true;
          }
        });

        if (saveVal)
          arrayData.push(0);
      }
      break;

    default:
      saveVal = true;
      for (let i = 0; i < 12; i++) {

        $.each(response, function (index, value) {

          if (value.label == i.toString()) {
            arrayData.push(value.acumulado);
            saveVal = false;
            return false;
          } else {
            saveVal = true;
          }
        });

        if (saveVal)
          arrayData.push(0);
      }

  }
  return arrayData;
}

function setDataKilometersToArray(response, typeChart) {

  let array = [];

  $.each(response, function (index, value) {
    array.push(value.acumulado);
  });

  return array;
}

/**
 * Funcitons to calculate the speed of the train
 */
function CalculateSpeed(latitud1, latitud2, longitud1, longitud2, d1, d2) {

  // Temporal Data
  // latitud1 = 20.643731;
  // // latitud2 = 20.621777;
  // latitud2 = 20.601167;

  // longitud1 = -103.437244;
  // // longitud2 = -103.400501;
  // longitud2 = -103.371710;

  // d1 = 1652774400;
  // // d2 = 1652775600; // 08:20
  // d2 = 1652775000; // 08:10
  // ENd

  // console.log("Data:", latitud1, latitud2, longitud1, longitud2, d1, d2);

  let date1 = tranformDate(new Date(d1 * 1000));
  let date2 = tranformDate(new Date(d2 * 1000));

  // console.log(date1, date2);

  let distance = calculateDistance(latitud1, longitud1, latitud2, longitud2);
  let seconds = timeTraveled(date1, date2);

  // console.log("Distancia:" + distance, "Segundos:" + seconds);

  // speed = (distance * 1000) / time; //Se obtiene la velocidad promedio en Metros sobre Segundos
  let speed = ((distance * 1000) / seconds) * 3.6;

  // console.log("Velocidad:" + speed);

  // console.log( "----------------------------------------------------------------------------------------------" );

  return speed.toFixed(1);
}

function calculateDistance(lat1, lon1, lat2, lon2) {

  Number.prototype.toRad = function () {
    return this * Math.PI / 180;
  }

  var R = 6371; // km
  //has a problem with the .toRad() method below.
  var x1 = lat2 - lat1;
  var dLat = x1.toRad();
  var x2 = lon2 - lon1;
  var dLon = x2.toRad();
  var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(lat1.toRad()) * Math.cos(lat2.toRad()) *
    Math.sin(dLon / 2) * Math.sin(dLon / 2);
  var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  var distance = R * c;

  return distance;
}

/**
 * Obtain time traveled for the train
 */
function timeTraveled(startD, finishD) {
// function timeTraveled(finishD, startD) {

  startD = startD.split(" ");
  finishD = finishD.split(" ");

  let date1 = startD[0];
  let date2 = finishD[0];

  var st = startD[1];
  var et = finishD[1];

  var rd = 0;
  var cd = 0;
  var dd = 1;

  date1 = date1.split("-");
  rd = date1[0];
  date1 = date1[2] + "-" + date1[1] + "-" + date1[0];
  date1 = new Date(date1);
  date1.setDate(date1.getDate() + 1);

  date2 = date2.split("-");
  cd = date2[0];
  date2 = date2[2] + "-" + date2[1] + "-" + date2[0];
  date2 = new Date(date2);
  date2.setDate(date2.getDate() + 1);

  dd = ((parseInt(cd) - parseInt(rd)) * 24);

  date1 = Date.parse(date1) / 1000;
  date2 = Date.parse(date2) / 1000;

  st = st.split(':');
  et = et.split(':');

  if (date1 < date2)
    et[0] = parseInt(et[0]) + parseInt(dd);

  var tt_h = parseInt(et[0]) - parseInt(st[0]);
  var tt_m = 0;
  var tt_s = 0;

  if (parseInt(et[1]) < parseInt(st[1]) && tt_h > 0) {

    tt_h -= 1;
    tt_m = (parseInt(et[1]) + 60) - parseInt(st[1]);
    tt_s = parseInt(et[2]) - parseInt(st[2]);

  } else {
    tt_m = parseInt(et[1]) - parseInt(st[1])
    tt_s = parseInt(et[2]) - parseInt(st[2]);
  }


  /**
   * Option to transform hours and minutes to seconds
   */
  var tts = 0;

  // Transform hour to minuts and add the minutes
  tts = (tt_h * 60) + tt_m;

  // Tranform minutos to second and add secconds
  tts = (tts * 60) + tt_s;

  return tts;
}

function tranformDate(date) {

  let day = date.getDate();
  let month = (date.getMonth() + 1);
  let year = date.getFullYear();

  let hour = date.getHours();
  let minutes = date.getMinutes();
  let seconds = date.getSeconds();

  date = day + "-" + month + "-" + year + " " + hour + ":" + minutes + ":" + seconds;

  return date;
}

function daysInMonth() {

  let dateSelected = $('#startDate .mx-datepicker .mx-input').val() != undefined ? $('#startDate .mx-datepicker .mx-input').val() : new Date() ;

  var dt = new Date( dateSelected );

  // dt.getMonth() will return a month between 0 - 11
  // we add one to get to the last day of the month
  // so that when getDate() is called it will return the last day of the month
  var month = dt.getMonth() + 1;
  var year = dt.getFullYear();

  // this line does the magic (in collab with the lines above)
  var daysInMonth = new Date(year, month, 0).getDate();

  let daysArray = []
  for (var i = 1; i <= daysInMonth; i++) {

    daysArray.push(i);
  }

  return daysArray;
}
