      this.lineChart.data.labels = labelsTest;
      this.lineChart.data.datasets[0].data = arrayDataLineChart[0][0];
      this.lineChart.data.datasets[1].data = arrayDataLineChart[0][3];
      this.lineChart.data.datasets[2].data = arrayDataLineChart[0][4];

