      <gmap-marker :position="center"> </gmap-marker>

      <!-- <gmap-polyline :key="index+'on'" v-for="(path, index) in paths"  v-bind:path.sync='path' v-bind:options="{ strokeColor: '#008000' }"></gmap-polyline>
      <gmap-polyline :key="index+'prod'" v-for="(path, index) in paths"  v-bind:path.sync='path' v-bind:options="{ strokeColor: '#F05432' }"></gmap-polyline> -->

      <gmap-polyline v-bind:path.sync='pathArranque' v-bind:options="{ strokeColor: '#008000' }"></gmap-polyline>
      <gmap-polyline id="polylineProd" :key="index+'prod'" v-for="(path, index) in pathsProduccion"  v-bind:path.sync='path' v-bind:options="{ strokeColor: '#F05432' }"></gmap-polyline>

      <!-- <div id="mapPolylines_1" :key="polyline" v-for="polyline in polylines"> {{polyline}} </div> -->
