      </gmap-marker> -->
      <!-- END -->

        <gmap-marker
          :key="m.lat"
          v-for="(m, index) in markers"
          :position="m.position"
          :aria-label="index"
          @click="toggleInfoWindow(m, index)"
          icon='http:\\maps.google.com/mapfiles/ms/icons/orange-dot.png'
        >
        </gmap-marker>

      <!-- Info window para mostrar la informacion detallada de las locomotoras -->
      <gmap-info-window
        :options="infoOptions"
        :position="infoWindowPos"
        :opened="infoWinOpen"
        @closeclick="infoWinOpen = false"
      >
        <div v-html="infoContent"></div>
      </gmap-info-window>

      <!-- Info Window para mostrar la informacion basica de las locomotoras -->
      <gmap-info-window
        :options="infoOptions"
        :position="infoWindowPos"
        :opened="infoWinOpen_LessInfo"
        @closeclick="infoWinOpen_LessInfo = true"
      >
        <div v-html="infoContent_LessInfo"></div>
      </gmap-info-window>
    </gmap-map>

    <div id="floating-panel" style="width: 28rem;">
      <div class="row">
        <div class="col-md-12">
          <select
            id="machines"
            class="form-control"
            style="width: 15rem !important"
            @change="obtainDataGps()"
          >
          </select>
        </div>

        <!-- <div class="col-md-6">

          <select id="zommMap" class="form-control" @change="zoomMap($event)">
            <option v-for="index in 20" :key="index" :value="index">{{index}}</option>
