import { API_KEY } from "./Maps/API_KEY";
import Vue from "vue";
import * as VueGoogleMaps from "vue2-google-maps";

import api from "./apiData/getApiData";

// import client, { paths2, paths3, latitud, longitud, markers} from './apiData/BackendData';

Vue.use(VueGoogleMaps, {
  load: {
    key: API_KEY
  }
});

export default {
  data() {
    return {
      intevalAdvance:null,

      // 20.674552, -103.387339
      center: {lat: 20.674552, lng: -103.387339},
      // center: {lat: 19.384460, lng: -99.181999}, // Coordenadas ciudad de mexico, depto en jose clemente orozco
      // path: [],
      // paths: [ [], [{lat: 20.67432055197465, lng: -103.38739022494786}, {lat: 20.652993320697036, lng: -103.39171329380791}] ],
      pathArranque: [],
      // pathsProduccion: [ [{lat: 20.67432055197465, lng: -103.38739022494786}, {lat: 20.652993320697036, lng: -103.39171329380791}] ],
      pathsProduccion: [],
      polylines: api.obtainPolylines(),
      // path2: paths2,
      // path3: paths3,
      zoomMapa: 11,
      options: {
        styles: [
          {
            featureType: "water",
            stylers: [
              { saturation: 43 },
              { lightness: -11 },
              { hue: "#0088ff" }
            ]
          },
          {
            featureType: "road",
            elementType: "geometry.fill",
            stylers: [
              { hue: "#ff0000" },
              { saturation: -100 },
              { lightness: 99 }
            ]
          },
          {
            featureType: "road",
            elementType: "geometry.stroke",
            stylers: [{ color: "#808080" }, { lightness: 54 }]
          },
          {
            featureType: "landscape.man_made",
            elementType: "geometry.fill",
            stylers: [{ color: "#ece2d9" }]
          },
          {
            featureType: "poi.park",
            elementType: "geometry.fill",
            stylers: [{ color: "#ccdca1" }]
          },
          {
            featureType: "road",
            elementType: "labels.text.fill",
            stylers: [{ color: "#767676" }]
          },
          {
            featureType: "road",
            elementType: "labels.text.stroke",
            stylers: [{ color: "#ffffff" }]
          },
          { featureType: "poi", stylers: [{ visibility: "off" }] },
          {
            featureType: "landscape.natural",
            elementType: "geometry.fill",
            stylers: [{ visibility: "on" }, { color: "#b8cb93" }]
          },
          { featureType: "poi.park", stylers: [{ visibility: "on" }] },
          {
            featureType: "poi.sports_complex",
            stylers: [{ visibility: "on" }]
          },
          { featureType: "poi.medical", stylers: [{ visibility: "on" }] },
          {
            featureType: "poi.business",
            stylers: [{ visibility: "simplified" }]
          }
        ]
      },
      infoContent: "",
      infoContent_LessInfo: "",
      infoWindowPos: {
        lat: 0,
        lng: 0
      },
      infoWinOpen: false,
      infoWinOpen_LessInfo: false,
      currentMidx: null,
      //optional: offset infowindow so it visually sits nicely on top of our marker
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35
        }
      },
      // markers: markers,
      markers: null
    };
  },
  methods: {
    // zoomMap(val){

    //   this.zoomMapa = parseInt(val.target.value);
    // },

    deployInfoWindows() {
      this.infoWinOpen_LessInfo = false;
    },

    toggleInfoWindow: function(marker, idx) {
      /**
       * Queda pendiente el detalle de reiniciar las variables a su estado original para que se muestren las "infoWindows" en el orden correcto.
       */

      this.infoWindowPos = marker.position;
      this.infoContent = this.getInfoWindowContent(marker);
      this.infoContent_LessInfo = this.getInfoWindowContentLessInfo(marker);

      //check if its the same marker that was selected if yes toggle
      if (
        /* this.currentMidx == idx && this.infoWinOpen && !this.infoWinOpen_LessInfo || */ this
          .currentMidx == idx &&
        !this.infoWinOpen &&
        this.infoWinOpen_LessInfo
      ) {
        this.infoWinOpen = !this.infoWinOpen;
        this.infoWinOpen_LessInfo = !this.infoWinOpen_LessInfo;
      } else if (
        this.currentMidx == idx &&
        this.infoWinOpen &&
        !this.infoWinOpen_LessInfo
      ) {
        this.infoWinOpen = false;
        this.infoWinOpen_LessInfo = true;
      }

      //if different marker set infowindow to open and reset current marker index
      else {
        this.infoWinOpen = false;
        this.infoWinOpen_LessInfo = true;
        this.currentMidx = idx;
      }
    },

    getInfoWindowContent: function(marker) {
      return `<div class="">
                    <!--<div>
                      <p class="title is-4">${marker.locomotora}</p>
                      <hr>
                    </div>-->

                  <div class="content">

                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Velocidad:</b> ${marker.velocidad} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Kilometraje:</b> ${marker.kilometraje} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Voltajes:</b> ${marker.voltajes} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Direccion:</b> ${marker.direccion} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Motor:</b> ${marker.motor} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Vel. Engranada:</b> ${marker.engranaje} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Nvl. Diesel (lts):</b> ${marker.lts_combustible} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Nvl. Diesel (%):</b> ${marker.n_combustible} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Operacion:</b> ${marker.arranque} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Operacion:</b> ${marker.operacion} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Fecha:</b> ${marker.fecha} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Geoposicion:</b> lat: ${marker.position["lat"]}, lng: ${marker.position["lng"]} </p>


                    <b>Fecha:</b>
                    <time datetime="2016-1-1">${marker.fecha}</time>

                  </div>
              </div>`;
    },

    getInfoWindowContentLessInfo: function(marker) {
      return `<div class="">
                    <!--<div>
                      <p class="title is-4">${marker.locomotora}</p>
                      <hr>
                    </div>-->

                  <div class="content">
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Velocidad:</b> ${marker.velocidad} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Nvl. Diesel (%):</b> ${marker.lts_combustible} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Nvl. Diesel (%):</b> ${marker.n_combustible} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Arranque:</b> ${marker.arranque} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Operacion:</b> ${marker.operacion} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Fecha:</b> ${marker.fecha} </p>
                  </div>
              </div>`;
    },

    setIntervalTimer() {
      this.intevalAdvance = setInterval(() => {
        this.obtainDataGps();
      }, 9000);
    },

    async obtainFirstLocation() {

      let firstLocation = JSON.parse(await api.obtainFirstLocation());

      try {
        this.center = firstLocation[0];
      } catch (error) {
        console.error(error);
      }
    },

    async obtainDataGps() {

      let dataCoordinates = JSON.parse(await api.obtainCoordinatesTrains( this.intevalAdvance ));
      this.pathsProduccion = [];

      try {
        this.pathArranque = dataCoordinates[0];

        for( let data of dataCoordinates[2] ){

          this.pathsProduccion.push(data);
        }

        this.markers = dataCoordinates[1];
      } catch (error) {
        console.error(error);
      }
    },
  },
  async mounted() {

    let login = localStorage.getItem("login_token");
    if (!login) {
      this.$router.push("/");
    }

    try {
      document.getElementsByClassName("ifLogin")[0].className = "noLogin";
    } catch (error) {
      console.error( error )
    }

    api.getLocomotoras();

    await this.obtainFirstLocation();
    await this.obtainDataGps();
    await this.setIntervalTimer();

  }
};
</script>

